import { DONG_AM, DONG_NGHIA, TRAI_NGHIA } from "constant/common";

function getItem(label, key, icon, children) {
    return {
      key,
      icon,
      children,
      label,
    };
}
  
export const MENUS = [
    getItem("ng m", DONG_AM),
    getItem("ng ngha", DONG_NGHIA),
    getItem("Tri ngha", TRAI_NGHIA),
];